body {
  background-color: #BDCDD6;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 20px;
  box-shadow: 0px 0px 5px 2px #7B8FA1;
}

h2 {
  text-shadow: 0 0 3px #7B8FA1;
}

.container {
  display: flex;
  justify-content: center;
  margin: 10px;
}

input {
  padding: 10px;
  margin: 1px;
  border-radius: 5px;
  border: solid 2px #7B8FA1;
  box-shadow: 0px 0px 5px 2px #7B8FA1;
}

button {
  padding: 10px;
  border-radius: 5px;
  border: solid 2px #7B8FA1;
  box-shadow: 0px 0px 5px 2px #7B8FA1;
}

.buttonItems {
  box-shadow: 4px 4px 25px black;
}

.items {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
  border: none;
  background-color: #7B8FA1;
  border-radius: 5px;
  width: max-content;
  box-shadow: 4px 4px 25px gray;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
  margin: 1px;
  background-color: rgb(208, 212, 215);
  border-radius: 5px;
  box-shadow: 4px 4px 25px black;
}

.picker {
  min-height: 100%;
}

.pickerDisable {
  display: none;
}

.spanDecoration {
  border-radius: 5px;
  text-transform: uppercase;
  padding: 3px;
  white-space: pre;
  background-color: #7B8FA1;
}

.react-datetime-picker__button {
  display: none;
}

.containerFilter {
  display: flex;
  justify-content: center;
}

.labelHistory {
  padding: 10px;
  margin: 1px;
  border-radius: 5px;
  border: solid 2px #7B8FA1;
}

.exsist {
  position: fixed;
  margin-top: -3px;
  margin-left: 10px;
  color: red;
  font-size: 10px;
}

.dotted {
  text-decoration: underline dotted red;
}

.undotted {
  text-decoration: none;
}

@media all and (max-width: 600px) {

.container {
  flex-wrap: wrap;
}
.item {
  flex-wrap: wrap;
  text-align: center;
}
.items {
  flex-wrap: wrap;
  width: 100%;
}
}
